import React, { Component } from "react";


import GitRepo from "../../components/GitRepo/GitRepo"
import Gallery from "../../components/Gallery/Gallery"
import ImageModal from "../../components/ImageModal/ImageModal"

import profile from "../../res/img/profile smaller.png"

// importing images


import "./HomePage.css"

export default class HomePage extends Component {

    state = {
        currentRepos: [],
        images: [],
        overlayImages: []
    }

    HomePage(props) {
        this.getGithubPages.bind(this);
        this.getImages.bind(this)
    }

    getGithubPages() {
        return(
            <>
                {this.state.currentRepos.slice(0,2).map(function(item, index){return (<GitRepo key={"key:" + index} gitObject={item} />)})}        
            </>
        )
    }

    getImages() {
        return(
            <>
                {this.state.images.map((item, index) => <ImageModal key={"key:" + index} src={item} overlaySrc={this.state.overlayImages[index]} alt="whoops. not found" className="images"/>)}
            </>
        )
    }

    componentDidMount() {
        fetch("https://api.github.com/users/michael-bailey/repos")
        .then(res => res.json())
        .then(result => {this.setState({currentRepos: result})})

        const imageContext = require.context("../../res/img/Gallery/", false, /\.jpe?g/)
        const overlayImageContext = require.context("../../res/img/Gallery/resized/", false, /\.jpe?g/)

        this.setState({
            images: imageContext.keys().splice(0, 6).sort().map(imageContext),
            overlayImages: overlayImageContext.keys().splice(0, 6).sort().map(overlayImageContext),
        });
    }

    render() {
        return(
            <>
                <img src={profile} alt="¯\_(ツ)_/¯" className="ProfileImage" />
                <h2>Home</h2>
                <p>This is a website dedicated to my projects and hobbies.<br/> as well as infomation about me.</p>
                <div className="sect ProgrammingSect">
                    <h3>Programming</h3>
                    <p>
                        Quite a lot of what i do in my spare time is programming.<br/>
                        This ranges from some test programs to try a specific area of a language or computing.<br/>
                        to a few larger projects (for example this website).
                    </p>
                    <h4>Current Repositories</h4>
                    <div className="RepoSect">
                        {this.getGithubPages()}
                    </div>
                </div>
                <div className="sect PhotographySect">
                    <h3>Photography</h3>
                    <p>
                        As a small side project i have started doing some simple photography.<br/>basiclly messing about with a camera taking pictures of plants as they make good desktop backgrounds.
                    </p>
                    <Gallery>
                        {this.getImages()}
                    </Gallery>
                </div>
            </>
        );
    }
};