import React, { Component } from "react";
import "./ImageModal.css"


export default class ImageModal extends Component {

    loadedImage = {};

    style = loading => {
        return {
          filter: `${loading ? 'blur(20px)' : 'blur(0px)'}`,
        }
      }

    state = {
        currentImage: this.props.overlaySrc,
        loading: true
    }

    componentDidMount() {
        this.getImage(this.props.src)
    }

    componentWillUnmount() {
        if (this.loadedImage) {
          this.loadedImage.onload = null
        }
      }

    getImage(src) {
        const image = new Image()
        image.onload = () => this.setState({currentImage: this.loadedImage.src, loading: false})
        image.src = src
        this.loadedImage = image
    }


    render() {
        return(
            <span>
                <img src={this.state.currentImage} alt={this.props.alt} className={this.props.className + " overlay"} style={this.style(this.state.loading)} />
            </span>
        )
    }
}

// heavily based on https://medium.com/@perjansson/a-progressive-image-loader-in-react-f14ae652619d